import axios from 'axios';
import { UseMutationOptions, useMutation } from 'react-query';

export const useDeleteOrder = (options: UseMutationOptions<unknown, unknown, string>) => {
  return useMutation('deleteOrder', async (orderId: string) => {
    const { data } = await axios.delete(
      `https://v3up8m4omj.execute-api.us-west-2.amazonaws.com/prod/order/${orderId}`,
    );
    return data;
  }, options);
};
