import { Autosuggest, TopNavigation } from '@cloudscape-design/components';
import {ReactComponent as CartIcon} from '../../assets/images/cart-icon.svg';
import houseIcon from '../../assets/images/house-icon.svg';
import { useNavigate } from 'react-router-dom';
import './style.css';
import { useGetCart } from '../../hooks/use-get-cart';
import { useCallback, useEffect, useMemo } from 'react';
import { useRecoilState } from 'recoil';
import cartIdAtom from '../../recoil/cart/atom';
import { useCreateCart } from '../../hooks/use-create-cart';

export const Navigation = () => {
  const navigate = useNavigate();
  const [cartId, setCartId] = useRecoilState(cartIdAtom);
  const { data, isLoading: isCartItemsLoading } = useGetCart(cartId);
  const { mutate: createCart } = useCreateCart({
    onSuccess: (cartId: any) => {
      setCartId(cartId);
    }
  });
  const cartItems = useMemo(() => data && data.CartItems ? data.CartItems: [], [data]);
  const isCartCheckedOut = useMemo(() => (!!(data && data.IsCheckedOut)), [data]);

  useEffect(() => {
    if (!cartId || isCartCheckedOut) {
      createCart();
    }
  }, [isCartCheckedOut]);

  const handleCartClick = useCallback(() => {
    navigate('/cart');
  }, [navigate]);

  const handleHeaderClick = useCallback((event: CustomEvent) => {
    event.preventDefault();
    navigate('/');
  }, [navigate]);

  const cartIconWithCount = useMemo((): React.ReactNode => (
    <>
      <div className="cart-icon">
        <CartIcon />
      </div>
      <span className='badge badge-warning' id='lblCartCount'>{isCartItemsLoading || !cartItems ? 0 : cartItems.length}</span>
    </>
  ), [cartItems, isCartItemsLoading]);

  return (
    <div className="top-nav">
      <TopNavigation
        identity={{
          href: '/',
          title: 'AnyHouse',
          logo: {
            src: houseIcon,
            alt: 'AnyHouse',
          },
          onFollow: handleHeaderClick,
        }}
        search={
          <Autosuggest
            value=""
            placeholder="Search AnyHouse"
            enteredTextLabel={value => `Search: "${value}"`}
          />
        }
        utilities={[
          {
            type: "menu-dropdown",
            iconName: "notification",
            ariaLabel: "Notifications (unread)",
            badge: true,
            disableUtilityCollapse: true,
            items: [
              {
                id: "Notification 1",
                text: "Order #1234567 has been delivered",
                items: [
                  {
                    id: "Notification Content 1",
                    text: "Your order #1234567 for 1 item(s) including \"98-inch Wide Sleeper Sofa\" has been delivered.",
                  }
                ]
              },
              {
                id: "Notification 2",
                text: "Notification 2",
                items: [
                  {
                    id: "Notification Content 1",
                    text: "blah blah blah blah blah blah blah blah blahblah blah blah blah blah blah blah blah blah",
                  }
                ]
              },
              {
                id: "Notification 3",
                text: "Notification 3",
                items: [
                  {
                    id: "Notification Content 1",
                    text: "blah blah blah blah blah blah blah blah blahblah blah blah blah blah blah blah blah blah",
                  }
                ]
              },
            ],
          },
          {
            type: "menu-dropdown",
            text: "Hello, Jacob",
            description: "jacho@amazon.com",
            iconName: "user-profile",
            onItemClick: () => navigate('/order'),
            items: [
              { id: "order history", text: "Order History" },
              { id: "profile", text: "Profile" },
              { id: "preferences", text: "Preferences" },
              {
                id: "support-group",
                text: "Support",
                items: [
                  {
                    id: "documentation",
                    text: "Documentation",
                    href: "#",
                    external: true,
                    externalIconAriaLabel:
                      " (opens in new tab)"
                  },
                  { id: "support", text: "Support" },
                  {
                    id: "feedback",
                    text: "Feedback",
                    href: "#",
                    external: true,
                    externalIconAriaLabel:
                      " (opens in new tab)"
                  }
                ]
              },
              { id: "signout", text: "Sign out" }
            ]
          },
          {
            type: "button",
            onClick: handleCartClick,
            iconSvg: cartIconWithCount,
            ariaLabel: "Cart",
            title: "Cart",
          },
        ]}
        i18nStrings={{
          searchIconAriaLabel: "Search",
          searchDismissIconAriaLabel: "Close search",
          overflowMenuTriggerText: "More",
          overflowMenuTitleText: "All",
          overflowMenuBackIconAriaLabel: "Back",
          overflowMenuDismissIconAriaLabel: "Close menu"
        }}
      />
    </div>
  );
}
