import {
  Box,
  Button,
  Cards,
  Header,
  SpaceBetween,
} from '@cloudscape-design/components';
import { UnifiedAppLayout } from '../../components/common/unified-app-layout';
import StarRatings from 'react-star-ratings';
import { Link } from 'react-router-dom';
import './style.css';
import cartIdAtom from '../../recoil/cart/atom';
import { useRecoilState } from 'recoil';
import { useGetCart } from '../../hooks/use-get-cart';
import { useEditCart } from '../../hooks/use-edit-cart';
import { useQueryClient } from 'react-query';
import { useMemo, useState } from 'react';
import { CartItem, Item, Product } from '../../types';
import { useListProducts } from '../../hooks/use-list-products';

export const Home = () => {
  const queryClient = useQueryClient();
  const [cartId, setCartId] = useRecoilState(cartIdAtom);
  const [lastClickedItem, setLastClickedItem] = useState<string>('');
  const { data: cartData, isLoading: isListCartItemsLoading } = useGetCart(cartId);
  const { data: productsData, isLoading: isListProductsLoading } = useListProducts();

  //console.log(process.env.PUBLIC_URL);

  const productItems = useMemo(() => {
    const results: Item[] = [];
    productsData?.forEach((product: Product)  => {
      results.push({
        asin: product.Asin,
        name: product.Name,
        image: require(`../../assets/images/${product.ImageName}.webp`),
        alt: product.Name,
        price: product.Price,
        reviewCount: product.ReviewCount,
        rating: product.Rating,
      })
    });
    return results;
  }, [productsData]);

  const cartItems: CartItem[] = useMemo(() => {
    return cartData?.CartItems ? cartData.CartItems : [];
  }, [cartData]);


  const { mutate: editCart, isLoading: isEditCartLoading } = useEditCart({
    onSuccess: () => {
      queryClient.invalidateQueries(['getCart', cartId]);
    }
  });

  const isItemInCart = (item: Item): boolean => {
    return !!cartItems.find((cartItem: CartItem) => {
      return cartItem.asin === item.asin;
    })
  }

  const onAddToCart = (item: Item) => {
    setLastClickedItem(item.name);
    if (isItemInCart(item)) {
      editCart({
        cartId,
        cartItems: cartItems.filter((cartItem: CartItem) => {
          return cartItem.asin !== item.asin;
        }),
        isCheckedOut: false,
      });
    } else {
      editCart({
        cartId,
        cartItems: [...cartItems, {asin:item.asin, name: item.name, price: item.price}],
        isCheckedOut: false,
      });
    }
  }

  const isAddToCartLoading = (item: Item): boolean => {
    return isEditCartLoading && item.name === lastClickedItem;
  }

  const addToCartButton = (item: Item) => {
    return isItemInCart(item) ? (
      <Button
        onClick={(e) => onAddToCart(item)}
        loading={isAddToCartLoading(item)}
        iconName="check"
        variant="primary"
      >
        Added
      </Button>
    ) : (
      <Button
        onClick={(e) => onAddToCart(item)}
        loading={isAddToCartLoading(item)}
      >
        Add to Cart
      </Button>
    );
  };

  return (
    <UnifiedAppLayout 
      content={
        <Cards<Item>
          ariaLabels={{
            itemSelectionLabel: (e, n) => `select ${n.name}`,
            selectionGroupLabel: 'Item selection'
          }}
          cardDefinition={{
            header: e => (
              <Link to='/cart'>
                <img src={e.image} alt={e.alt} style={{objectFit: 'cover', minWidth: '100%', height: 'auto', maxWidth: '100%', maxHeight: '100%'}}/>
              </Link>
            ),
            sections: [
              {
                id: "name",
                content: e => (
                  <Link
                    to="/cart"
                    style={{textDecoration: 'none'}}
                  >
                    <div
                      style={
                        {
                          marginTop: -8,
                          marginBottom: -16,
                          textDecoration: "none"
                        }
                      }
                    >
                      <Box variant="h3">
                        {e.name}
                      </Box>
                    </div>
                  </Link>
                ),
              },
              {
                id: "price",
                content: e => (
                  <Link
                    to="/cart"
                    style={{textDecoration: 'none'}}
                  >
                    <div style={{marginTop: -4, marginBottom: -16}}>
                      <Box variant="p" fontSize='heading-m'>
                        {`$ ${e.price}`}
                      </Box>
                    </div>
                  </Link>
                ),
              },
              {
                id: "rating",
                content: item => (
                  <div style={{marginTop: -4, marginBottom: -8}}>
                    <div style={{display: "flex", justifyContent: "space-between"}}>
                      <SpaceBetween size="xs" direction="horizontal">
                        <Box variant="p" display="inline-block" fontSize='body-m'>
                          {item.rating}
                        </Box>
                        <Box variant="div" padding={{top: 'xxs'}}>
                          <StarRatings
                            rating={item.rating}
                            starRatedColor='rgb(230, 67, 47)'
                            starDimension="16px"
                            starSpacing="0"
                          />
                        </Box>
                        <Link
                          to="/cart"
                          style={{textDecoration: 'none'}}
                        >
                          <Box variant="p" display="inline-block" fontSize="body-m" color="text-status-info">
                            <div className='reviews'>
                              {`(${item.reviewCount})`}
                            </div>
                          </Box>
                        </Link>
                      </SpaceBetween>
                      {addToCartButton(item)}
                    </div>
                  </div>
                ),
              },
            ]
          }}
          cardsPerRow={[
            { cards: 1 },
            { minWidth: 500, cards: 3 }
          ]}
          items={productItems}
          loadingText="Loading resources"
          trackBy="name"
          visibleSections={["name", "price", "rating"]}
          empty={
            <Box textAlign="center" color="inherit">
              <b>No products</b>
              <Box
                padding={{ bottom: "s" }}
                variant="p"
                color="inherit"
              >
                No products to display.
              </Box>
              <Button>Refresh</Button>
            </Box>
          }
          header={
            <Header
              description={
                <Box variant='div' margin={{vertical: 's'}}>
                  Today's hot deals
                </Box>
              }
            >
              Hot Deals
            </Header>
          }
        />
      }
    />
  );
}
