import axios from 'axios';
import { useQuery } from 'react-query';

export const useListProducts = () => {
  return useQuery('listProducts', async () => {
    const { data } = await axios.get(
      'https://v3up8m4omj.execute-api.us-west-2.amazonaws.com/prod/product'
    );
    return data;
  });
};
