import axios from 'axios';
import { useQuery } from 'react-query';

export const useGetCart = (cartId: string) => {
  return useQuery(['getCart', cartId], async () => {
    if (!cartId) {
      return undefined;
    }
    const { data } = await axios.get(
      `https://v3up8m4omj.execute-api.us-west-2.amazonaws.com/prod/cart/${cartId}`
    );
    return data;
  });
};
