import axios from 'axios';
import { UseMutationOptions, useMutation } from 'react-query';
import { CartItem } from '../types';

export type EditCartProps = {
  cartId: string,
  cartItems: CartItem[],
  isCheckedOut: boolean,
}

export const useEditCart = (options: UseMutationOptions<unknown, unknown, EditCartProps>) => {
  return useMutation('editCart', async (editCartProps: EditCartProps) => {
    const { cartId, cartItems, isCheckedOut } = editCartProps;
    const { data } = await axios.put(
      `https://v3up8m4omj.execute-api.us-west-2.amazonaws.com/prod/cart/${cartId}`,
      {
        cartItems,
        isCheckedOut,
      }
    );
    return data;
  }, options);
};
