import axios from 'axios';
import { UseMutationOptions, useMutation } from 'react-query';
import { CartItem } from '../types';

export const useCreateOrder = (options: UseMutationOptions<unknown, unknown, CartItem[]>) => {
  return useMutation('createOrder', async (items: CartItem[]) => {
    let totalCost = 0.00;
    if (!items || !items.length) {
      totalCost = 0.00;
    } else {
      for (const item of items) {
        totalCost += item.price;
      }
    }

    const { data } = await axios.post(
      'https://v3up8m4omj.execute-api.us-west-2.amazonaws.com/prod/order',
      {
        items,
        totalCost,
      }
    );
    return data;
  }, options);
};
