import { 
  AppLayout,
  Box,
  Button,
  Container,
  ContentLayout,
  Grid,
  SpaceBetween
} from '@cloudscape-design/components';
import { Navigation } from './navigation';
import { useNavigate } from 'react-router-dom';
import { useCallback } from 'react';

export type UnifiedAppLayoutProps = {
  content: JSX.Element
}

export const UnifiedAppLayout = ({content}: UnifiedAppLayoutProps) => {
  const navigate = useNavigate();

  const onHotDealsClick = useCallback(() => {
    navigate('/');
  }, [navigate]);

  return (
    <AppLayout
      content={
        <ContentLayout
          header={
            <Navigation />
          }
        >
          <SpaceBetween size="m">
          <Container>
            <Grid
              gridDefinition={[{ colspan: 3 }, { colspan: 3 }, { colspan: 3 }, { colspan: 3 }]}
            >
              <Box textAlign='center'>
                <Button variant="primary" onClick={onHotDealsClick}>
                  Hot Deals
                </Button>
              </Box>
              <Box textAlign='center'>
                <Button variant="primary">
                  Furnitures
                </Button>
              </Box>
              <Box textAlign='center'>
                <Button variant="primary">
                  Interior Items
                </Button>
              </Box>
              <Box textAlign='center'>
                <Button variant="primary">
                  Electronics
                </Button>
              </Box>
            </Grid>
          </Container>
          {content}
          </SpaceBetween>
        </ContentLayout>
      }
      navigationHide={true}
      toolsHide={true}
    />
  );
}
