import {
  Box,
  Button,
  Container,
  Header,
  SpaceBetween,
  Spinner
} from '@cloudscape-design/components';
import { useMemo, useState } from 'react';
import { UnifiedAppLayout } from '../../components/common/unified-app-layout';
import { useListOrders } from '../../hooks/use-list-orders';
import { OrderEntity } from '../../types';
import { useDeleteOrder } from '../../hooks/use-delete-order';
import { useQueryClient } from 'react-query';

export const Order = () => {
  const { data, isLoading } = useListOrders();
  console.log(data);
  const queryClient = useQueryClient();

  const { mutate: deleteOrder, isLoading: isDeleteOrderLoading } = useDeleteOrder({
    onSuccess: () => {
      queryClient.invalidateQueries('listOrders');
    }
  });

  const [lastClickedOrder, setLastClickedOrder] = useState<string>('');

  const orders = useMemo(() => {
    return isLoading ? [] : data.map((order: OrderEntity, index: number) => {
      return (
        <Box variant="div" margin={{ bottom: "l" }}>
          <Container>
            <Box variant="div">
              <Box variant="h2">
                {`Order - ${order.OrderId}`}
              </Box>
              <hr />
              <Box variant="h3">
                {"Items"}
              </Box>
              <hr />
              <SpaceBetween size="l">
                <Box variant="div">
                  {order.items.map((orderItem) => (
                    <Box variant="div">
                      <Box variant="span">
                        {orderItem.name}
                      </Box>
                      <Box variant="span" float="right">
                        {`$ ${orderItem.price}`}
                      </Box>
                    </Box>
                  ))}
                </Box>
                <Box variant="div">
                  <hr />
                  <Box variant="span">
                    Total Cost
                  </Box>
                  <Box variant="span" float="right">
                    {`$ ${order.totalCost.toFixed(2)}`}
                  </Box>
                </Box>
              </SpaceBetween>
              <hr />
              <Box variant='div' textAlign='right'>
                <Button
                  loading={isDeleteOrderLoading && lastClickedOrder === order.OrderId}
                  onClick={() => {
                    setLastClickedOrder(order.OrderId);
                    deleteOrder(order.OrderId);
                  }}
                >
                  Delete
                </Button>
              </Box>
            </Box>
          </Container>
        </Box>
      );
    });
  }, [data, isLoading]);

  return (
    <UnifiedAppLayout
      content={
        <Container
          header={
            <SpaceBetween size="l">
              <Header>
                Order History
              </Header>
              <Box variant="div" />
            </SpaceBetween>
          }>
          {isLoading && <Spinner size="big" />}
          {orders}
        </Container>
      }
    />
  );
}
