import {
  Alert,
  Box,
  Button,
  Container,
  Grid,
  Header,
  SpaceBetween,
  Spinner
} from '@cloudscape-design/components';
import { useCallback, useMemo } from 'react';
import { UnifiedAppLayout } from '../../components/common/unified-app-layout';
import { useGetCart } from '../../hooks/use-get-cart';
import { CartItem, Product } from '../../types';
import { useCreateOrder } from '../../hooks/use-create-order';
import { useNavigate } from 'react-router-dom';
import { useRecoilState } from 'recoil';
import cartIdAtom from '../../recoil/cart/atom';
import { useQueryClient } from 'react-query';
import { useEditCart } from '../../hooks/use-edit-cart';
import { useListProducts } from '../../hooks/use-list-products';

export const Cart = () => {
  const queryClient = useQueryClient();
  const navigate = useNavigate();
  const [cartId, setCartId] = useRecoilState(cartIdAtom);
  const { data: cartData, isLoading: isListCartItemsLoading } = useGetCart(cartId);
  const { mutate: editCart, isLoading: isEditCartLoading } = useEditCart({
    onSuccess: () => {
      queryClient.invalidateQueries(['getCart', cartId]);
    }
  });
  const { data: productsData, isLoading: isListProductsLoading } = useListProducts();

  const cartItems = useMemo(() => {
    return cartData?.CartItems ? cartData.CartItems : [];
  }, [cartData]);

  const {
    mutate: createOrder,
    isLoading: isCreateOrderLoading,
    isError: isCreateOrderError
  } = useCreateOrder({
    onSuccess: () => {
      editCart({
        cartId,
        cartItems,
        isCheckedOut: true
      })
      navigate('/checkout');
    }
  });

  const onRemoveFromCart = useCallback((item: CartItem) => {
    editCart({
      cartId,
      cartItems: cartItems.filter((cartItem: CartItem) => {
        return cartItem.asin !== item.asin;
      }),
      isCheckedOut: false,
    });
  }, [cartId, cartItems, editCart]);

  const onEmptyCart = useCallback(() => {
    editCart({
      cartId,
      cartItems: [],
      isCheckedOut: false,
    });
  }, [cartId, editCart]);

  const cartItemTiles = useMemo(() => {
    return isListCartItemsLoading && isListProductsLoading ? [] : cartItems.map((item: CartItem, index: number) => {
      const productInfo = productsData.find((product: Product) => {
        return product.Asin === item.asin;
      })
      return (
        <Box variant="div" margin={{ bottom: "l" }}>
          <Container>
            <Grid
              gridDefinition={[{ colspan: 2 }, { colspan: 10 }]}
            >
              <img src={require(`../../assets/images/${productInfo.ImageName}.webp`)} alt={`sofa${index}`} style={{objectFit: "cover", minWidth: "100%", height: "auto", maxWidth: "100%", maxHeight: "100%"}}/>
              <SpaceBetween size="s">
                <Box variant="h2">
                  {item.name}
                </Box>
                <Box variant="div">
                  {`$ ${item.price}`}
                </Box>
                <Box variant="div" textAlign="left">
                  <Button
                    loading={isCreateOrderLoading || isEditCartLoading}
                    onClick={(e) => onRemoveFromCart(item)}
                  >
                    Remove from Cart
                  </Button>
                </Box>
              </SpaceBetween>
            </Grid>
          </Container>
        </Box>
      );
    });
  }, [cartItems, isCreateOrderLoading, isEditCartLoading, isListCartItemsLoading, isListProductsLoading, onRemoveFromCart, productsData]);

  const getTotalCosts = useCallback(() => {
    if (!cartItems || !cartItems.length) {
      return 0.00;
    }
    let totalCost = 0;
    for (const item of cartItems) {
      totalCost += item.price;
    }

    return totalCost.toFixed(2);
  }, [cartItems]);

  const onProceedToCheckOut = useCallback(async () => {
    createOrder(cartItems);
  }, [cartItems, createOrder]);

  return (
    <UnifiedAppLayout
      content={
        <Grid
          gridDefinition={[{ colspan: 9 }, { colspan: 3 }]}
        >
        <Container
          header={
            <SpaceBetween size="l">
              <Header
                counter={cartItems ? `(${cartItems.length})` : ''}
              >
                Shopping Cart
              </Header>
              {isCreateOrderError &&
                <Alert type="error">
                  {'There was an error while submitting the order! Please try again in a few minutes.'}
                </Alert>
              }
              <Box variant="div" />
            </SpaceBetween>
          }>
          {isListCartItemsLoading && <Spinner size="big" />}
          {!isCreateOrderError && cartItems.length === 0 &&
              <>
                <Box variant="p">
                  {'You have an empty cart : >'}
                </Box>
                <Box variant="p">
                {'Add something to your cart and come back !'}
                </Box>
              </>
            }
          {cartItemTiles}
          {cartItems.length !== 0 &&
            <Box variant='div' textAlign='right'>
              <Button
                onClick={onEmptyCart}
                loading={isCreateOrderLoading || isEditCartLoading}
              >
                Empty Cart
              </Button>
            </Box>
          }
        </Container>
        <Container
          header={
            <SpaceBetween size="s">
              <Box variant="h4">
                Order summary
              </Box>
              <hr />
            </SpaceBetween>
          }>
          {isListCartItemsLoading && <Spinner size="big" />}
          <SpaceBetween size="s">
            {cartItems ? cartItems.map((item: CartItem) => (
              <Box variant="div">
                <Box variant="span">
                  {item.name}
                </Box>
                <Box variant="span" float="right">
                  {`$ ${item.price}`}
                </Box>
              </Box>  
            )) : ''}
            <hr />
            <Box variant="div">
              <Box variant="span" color="text-status-inactive">
                Delivery
              </Box>
              <Box variant="span" float="right" color="text-status-inactive">
                $ 0.00
              </Box>
            </Box>
            <hr />
            <Box variant="div">
              <Box variant="span" fontWeight='bold'>
                Total
              </Box>
              <Box variant="span" float="right">
                {`$ ${getTotalCosts()}`}
              </Box>
            </Box>
            <Box variant="div" textAlign="center">
              <Button
                variant="primary"
                onClick={onProceedToCheckOut}
                loading={isCreateOrderLoading || isEditCartLoading}
                disabled={cartItems.length === 0}
              >
                Proceed to checkout
              </Button>
            </Box>
          </SpaceBetween>
        </Container>
        </Grid>
      }
    />
  );
}
